@media screen and (width <= 1199px) {
  .tags {
    width: 100vw;
    padding-top: 30px;
    position: relative;
    overflow: hidden;
  }

  .tags__track {
    align-items: center;
    width: max-content;
    animation: 25s linear infinite tags-infinite-scroll;
    display: flex;
  }

  @keyframes tags-infinite-scroll {
    0% {
      transform: translateX(0);
    }

    100% {
      transform: translateX(-50%);
    }
  }
}

@media screen and (width >= 1200px) {
  .tags__item:first-child {
    animation: 1.2s ease-in-out infinite wiggleBtn;
  }

  .tags__item:nth-child(2) {
    animation: 1.3s ease-in-out infinite wiggleBtn;
  }

  .tags__item:nth-child(3) {
    animation: 2.8s ease-in-out infinite wiggleBtn35deg;
  }

  .tags__item:nth-child(4) {
    animation: 1.1s ease-in-out infinite wiggleBtn;
  }

  .tags__item:nth-child(5) {
    animation: 2.2s ease-in-out infinite wiggleBtn17deg;
  }

  .tags__item:nth-child(6) {
    animation: 2.5s ease-in-out infinite wiggleBtn132deg;
  }

  .tags__item:nth-child(7) {
    animation: 2s ease-in-out infinite wiggleBtn180deg;
  }

  .tags__item:nth-child(8) {
    animation: 1.5s ease-in-out infinite wiggleBtn;
  }

  .tags__item:nth-child(9) {
    animation: 2.8s ease-in-out infinite wiggleBtn35deg;
  }

  .tags__item:nth-child(10) {
    animation: 2s ease-in-out infinite wiggleBtn180deg;
  }

  .tags__item:nth-child(11) {
    animation: 3.1s ease-in-out infinite wiggleBtn149deg;
  }

  .tags__item:nth-child(12) {
    animation: 2s ease-in-out infinite wiggleBtn180deg;
  }

  @keyframes wiggleBtn {
    0% {
      transform: rotate(-8deg);
    }

    25% {
      transform: rotate(8deg);
    }

    50% {
      transform: rotate(-8deg);
    }

    75% {
      transform: rotate(8deg);
    }

    100% {
      transform: rotate(-8deg);
    }
  }

  @keyframes wiggleBtn35deg {
    0% {
      transform: rotate(-43deg);
    }

    25% {
      transform: rotate(43deg);
    }

    50% {
      transform: rotate(-43deg);
    }

    75% {
      transform: rotate(43deg);
    }

    100% {
      transform: rotate(-43deg);
    }
  }

  @keyframes wiggleBtn17deg {
    0% {
      transform: rotate(-25deg);
    }

    25% {
      transform: rotate(25deg);
    }

    50% {
      transform: rotate(-25deg);
    }

    75% {
      transform: rotate(25deg);
    }

    100% {
      transform: rotate(-25deg);
    }
  }

  @keyframes wiggleBtn132deg {
    0% {
      transform: rotate(132deg);
    }

    25% {
      transform: rotate(70deg);
    }

    50% {
      transform: rotate(132deg);
    }

    75% {
      transform: rotate(70deg);
    }

    100% {
      transform: rotate(132deg);
    }
  }

  @keyframes wiggleBtn180deg {
    0% {
      transform: rotate(180deg);
    }

    25% {
      transform: rotate(115deg);
    }

    50% {
      transform: rotate(180deg);
    }

    75% {
      transform: rotate(115deg);
    }

    100% {
      transform: rotate(180deg);
    }
  }

  @keyframes wiggleBtn149deg {
    0% {
      transform: rotate(149deg);
    }

    25% {
      transform: rotate(207deg);
    }

    50% {
      transform: rotate(149deg);
    }

    75% {
      transform: rotate(207deg);
    }

    100% {
      transform: rotate(149deg);
    }
  }
}

